.grid {
        display: grid;
        grid-template-columns: 2fr 3fr;
        justify-items: stretch;
        -webkit-box-align: stretch;
            -ms-flex-align: stretch;
                align-items: stretch;
        height: 100vh;
        grid-template-rows: 1fr;
        overflow: hidden;
}
.text-content{
        grid-column: 1/2;
        grid-row: 1;
        background-color: black;
        color: white;
        display: grid;
        grid-template-columns: 1fr 2fr 2fr 1fr 2fr 2fr 1fr;
        grid-template-rows: auto;
        padding-top: 50px;
}
.text-heading{
        grid-column: 2/7;
        text-align: left;
        font-weight: 800;
        font-size: 3em;
        -ms-flex-item-align: center;
            align-self: center;
}
.text-body{
        grid-column: 2/6;
        text-align: left;
        font-size: 1.3em;
        font-weight: 500;
        padding-top: 0px;
        margin-top: 0px;
        margin-bottom: 0px;
        padding-bottom: 0px;
        line-height: 1.3em;
        opacity: 0.9;
}
.thankyou{
        grid-column: 1/3;
        grid-row: 1;
        background-color: #cfbb39;
        display: grid;
